import React from 'react';
import { Route, Switch } from 'react-router-dom';
import Face_enrollment from '../../Views/Face_enrollment/Face_enrollment';
import Home from '../../Views/Home/Home';
import Mark_attendance from '../../Views/Mark_attendance/Mark_attendance';
import Registration from '../../Views/Registration/Registration';
import Header from '../Header/Header';
import SideBar from '../SideBar/SideBar';
const Layouts = (props) => {
    
    const user = localStorage.getItem('orgid')

    if (!user) {
        props.history.replace('/login')
    }

    return (
        <>
            <SideBar />
            <main className="main-content mt-1 border-radius-lg">
                <Header />
                <Switch>
                    <Route path="/home" exact strict component={Home} />
                    <Route exact path="/registration" render={props => <Registration {...props} />} />
                    <Route exact path="/face_enrollment" render={props => <Face_enrollment {...props} />} />
                    <Route exact path="/mark_attendance" render={props => <Mark_attendance {...props} />} />
                    {/* <Redirect to="/login" from="*" /> */}
                </Switch>
            </main>

        </>
    )
}

export default Layouts;