import PhotoCameraRoundedIcon from "@material-ui/icons/PhotoCameraRounded";
import { Button, Dialog, DialogActions, DialogContent, DialogContentText, DialogTitle, Modal, Typography } from '@material-ui/core';
import { useLoading, Audio, BallTriangle } from '@agney/react-loading';


