// Upload to local seaweedFS instance

function Face_enrollment(props) {

    const myState = useSelector((state) => state.vbas_store)
    const dispatch = useDispatch();
    const [source, setSource] = useState("");
    const [userData, setUserData] = useState({ attdid: '' });
    const orgid = localStorage.getItem('orgid')
    const [open, setOpen] = React.useState(false);
    const [base64_image, setBase64_image] = useState('');
    const snapSoundElement = document.getElementById('snapSound');
    const [Loader, setLoader] = useState(false);
    const { containerProps, indicatorEl } = useLoading({
        loading: true,
        indicator: <BallTriangle width="50" />,
    });
    // Capture image from video

    const webcamRef = React.useRef(null);

    const capture = React.useCallback(
        () => {
            if (snapSoundElement != null) {
                snapSoundElement.play();
            }
            const imageSrc = webcamRef.current.getScreenshot({ width: 1920, height: 1080 });
            const did = localStorage.getItem('did_from_login')
            const data = { "txnid": did, "image": imageSrc.slice(23) }
            setBase64_image(imageSrc)
            dispatch(FaceEmbedAction(data))
            if (myState.FaceEmbedReducer.ftemplate) {
                setLoader(true)
            }

        },
        [webcamRef]
    );

    const videoConstraints = {
        width: 1280,
        height: 720,
        facingMode: "user"
    };

    const handleSubmit = async () => {
        const did = localStorage.getItem('did_from_login')
        const data = { "did": did, "attdid": orgid + "-" + userData.attdid }
        dispatch(FaceEnGetDetailsAction(data))
    }

    const handleClickOpen = (e) => {
        e.preventDefault();
        setOpen(true);

    };

    const handleClose = (value) => {
        setOpen(false);
    };

    const handleClear = () => {
        setBase64_image("");
    }

    const getBase64 = file => {
        return new Promise(resolve => {
            let fileInfo;
            let baseURL = "";
            let reader = new FileReader();
            reader.readAsDataURL(file);
            reader.onload = () => {
                baseURL = reader.result;
                resolve(baseURL);
            };
        });
    };

    const handleCapture = (target) => {
        if (target.files) {
            if (target.files.length !== 0) {
                const file = target.files[0];
                // if you want to create image link use below line
                // const newUrl = URL.createObjectURL(file);
                getBase64(file).then(result => {
                    file["base64"] = result;
                    setSource(file)

                    dispatch(CameraAction(file.base64.slice(22)))
                }).catch(err => {
                    console.log(err)
                })
            }
        }
    };

    const secHandleSubmit = async () => {
        const did = localStorage.getItem("did_from_login")
        const ftemplate = [myState.FaceEmbedReducer.data]
        const data = { "did": did, "attdid": orgid + "-" + userData.attdid, "fimg": base64_image.slice(23), "refimg": myState.cameraRed.slice(1), "ftemplate": ftemplate.toString() }
        // const data = { "did": did, "attdid": orgid + "-" + userData.attdid, "fimg": myState.cameraRed.slice(1), "refimg": myState.cameraRed.slice(1), "ftemplate": myState.FaceEmbedReducer.ftemplate }

        console.log(data);
        dispatch(EnrollAttendeFaceAction(data))
        setBase64_image("")
        setUserData({ attdid: "" })
        if (myState.EnrollFaceReducer.respcode === "200") {
            toast.success("success")
        }
    }

    const formFields = [
        {
            size: 13,
            field: (
                // <TextField id="outlined-search" label="Ord ID" type="search" variant="outlined" value={localStorage.getItem('orgid')} onChange={() => setUserData({ ...userData, did: localStorage.getItem('did_from_login') })} />
                <>
                    <div className="col-md-6">
                        <div className="form-group">
                            <label htmlFor="Input1">Organization ID</label>
                            <input type="text" value={localStorage.getItem('orgid') + "-"} className="form-control" id="Input1" placeholder={1234} readOnly />
                        </div>
                    </div>
                    <div className="col-md-6">
                        <div className="form-group">
                            <label htmlFor="Input1">Attendee ID Sequence</label>
                            <input type="text" value={userData.attdid} onChange={(e) => setUserData({ ...userData, attdid: e.target.value })} className="form-control" id="Input1" />
                        </div>
                    </div>
                </>
            ),
        },
    ];

    const formFieldsTwo = [
        {
            size: 13,
            field: (
                <>
                    <div className="col-md-3">
                        <div className="form-group">
                            <label htmlFor="Input3">Attendee Name</label>
                            <input type="text" className="form-control" id="Input3" value={myState.FaceGetDetails.attdname} placeholder="Attendee Name" readOnly />
                        </div>
                    </div>
                    <div className="col-md-3">
                        <div className="form-group">
                            <label htmlFor="Input4">Attendee ID/Code</label>
                            <input type="text" className="form-control" id="Input4" value={myState.FaceGetDetails.attdcode} placeholder="Attendee ID/Code" readOnly />
                        </div>
                    </div>
                    <div className="col-md-3">
                        <div className="form-group">
                            <label htmlFor="Input5">Class</label>
                            <input type="text" className="form-control" id="Input5" value={myState.FaceGetDetails.attdorg} placeholder="Class Name" readOnly />
                        </div>
                    </div>
                    <div className="col-md-3">
                        <div className="form-group">
                            <label htmlFor="Input6">Category</label>
                            <input type="text" className="form-control" id="Input6" value={myState.FaceGetDetails.attdcategory} placeholder="Category" readOnly />
                        </div>
                    </div>


                    <div className="col-md-2 ">
                    </div>
                    <div className="col-md-4 ">
                        <div className="card-body cus-div">
                            {source &&
                                <p>Image Uploaded</p>
                                // show image
                                //   <Box display="flex" justifyContent="center" border={1} className={classes.imgBox}>
                                // <img src={source} alt={"snap"} className={classes.img}></img>
                                // </Box>
                            }
                            <input
                                accept="image/*"
                                style={{ display: "none" }}
                                id="icon-button-file"
                                type="file"
                                capture="environment"
                                onChange={(e) => handleCapture(e.target)}
                            />
                            <label htmlFor="icon-button-file">
                                <PhotoCameraRoundedIcon fontSize="large" color="primary" />
                            </label>
                        </div>
                    </div>
                    <div className="col-md-4">
                        <div className="card-body cus-div" >
                            <Button className="w-100" color="primary" onClick={handleClickOpen}>
                                Open Camera
                            </Button>
                            <Dialog

                                open={open}
                                onClose={handleClose}
                                aria-labelledby="alert-dialog-title"
                                aria-describedby="alert-dialog-description"
                            >
                                <DialogTitle id="alert-dialog-title"></DialogTitle>
                                <DialogContent className="p-0 px-3">
                                    <DialogContentText id="alert-dialog-description" style={{ width: "580px", height: "500px", margin: '0px' }}>
                                        {base64_image ? <img src={`${base64_image}`} style={{ borderRadius: '5px', marginLeft: '5px', width: "560px", height: "356px", bordeRadius: "5px" }} alt="no img" /> :
                                            <div>
                                                <Webcam
                                                    className="w-100"
                                                    width={350}
                                                    height={300}
                                                    ref={webcamRef}
                                                    screenshotFormat="image/jpeg"
                                                    videoConstraints={videoConstraints}
                                                />
                                                {/* <section {...containerProps}>
                                                    {/* renders only while loading */}
                                                {/* {indicatorEl}  */}
                                                {/* </section>  */}
                                                <button className="btn bg-gradient-info w-50" style={{ margin: 'auto', justifyContent: 'center', display: 'flex', marginTop: '15px' }} onClick={capture}>  Capture photo</button>


                                            </div>
                                        }
                                    </DialogContentText>
                                </DialogContent>
                                <DialogActions>
                                    <Button className="btn bg-gradient-info w-50 mb-3 mx-3" onClick={handleClose} color="primary">
                                        Submit
                                    </Button>
                                    <Button className="btn bg-gradient-info w-50 mb-3 mx-3" onClick={handleClear} color="primary">
                                        Re-Capture
                                    </Button>
                                </DialogActions>
                            </Dialog>

                        </div>
                    </div>
                </>
            ),
        },
    ];

    return (
        <Aux>
      {myState.isSuccess ? "loading...": null}
      {console.log(myState.isSuccess)}
            <div className="container-fluid py-4 ">
                <div className="row">
                    <div className="col-12">
                        <div className="card ">
                            <div className="card-header pb-0 p-3">
                                <h6 className="mb-0">Attendee Face Enrollment</h6>
                            </div>
                            <div className="card-body p-3">
                                <form>
                                    {formFields.map((item, idx) => (
                                        <div className="row" item xs={item.size} key={idx}>
                                            {item.field}
                                        </div>
                                    ))}
                                    <div className="text-center">
                                        <button type="button" onClick={handleSubmit} className="btn bg-gradient-info w-100 mt-1 mb-0">Get Details</button>
                                    </div>
                                </form>
                                <br />
                                <form>
                                    {formFieldsTwo.map((item, idx) => (
                                        <div className="row" item xs={item.size} key={idx}>
                                            {item.field}
                                        </div>
                                    ))}
                                    <div className="text-center">
                                        <button type="button" onClick={secHandleSubmit} className="btn bg-gradient-info w-100 mt-4 mb-0">Enroll Face</button>
                                    </div>
                                </form>
                            </div>
                        </div>
                    </div>
                </div>
            </div>
            <ToastContainer />
        </Aux>
    );
}

export default Face_enrollment;