import React, { useEffect } from 'react';
import '../../App.css';
import 'react-toastify/dist/ReactToastify.css';
import Aux from '../../hoc/Aux';
import { useDispatch, useSelector } from "react-redux";
import { ResetLogin, SyncDataAction } from '../../Store/Actions/action';
import { ToastContainer } from 'react-toastify';
import 'react-toastify/dist/ReactToastify.css';

const Home = (props) => {
  const dispatch = useDispatch()
  const loader = useSelector((state) => state.vbas_store)

  const did = localStorage.getItem('did_from_login')
  const orgid = localStorage.getItem('orgid')
  const orgname = localStorage.getItem('orgname')



  const syncData = () => {
    const data = { "did": did, "lastsyncdate": Date().toLocaleString().slice(0, 24), "syncflag": "0" }
    dispatch((SyncDataAction(data)))
  }

  useEffect(() => {
    syncData()
    dispatch(ResetLogin())
  }, [])

  return (
    <Aux>
      {loader.isSuccess ? "loading...": null}
      <div className="container-fluid py-4">
        <div className="row">
          <div className="col-12 col-xl-3">
            <div className="card ">
              <div className="card-header pb-0 p-3">
                <h6 className="mb-0">Device ID</h6>
              </div>
              <div className="card-body p-3">

                <h4 className="text-sm">
                  {did}
                </h4>
              </div>
            </div>
          </div>
          <div className="col-12 col-xl-3">
            <div className="card ">
              <div className="card-header pb-0 p-3">
                <h6 className="mb-0">Organization ID</h6>
              </div>
              <div className="card-body p-3">

                <h4 className="text-sm">
                  {orgid}
                </h4>
              </div>
            </div>
          </div>
          <div className="col-12 col-xl-3">
            <div className="card ">
              <div className="card-header pb-0 p-3">
                <h6 className="mb-0">Organization Name</h6>
              </div>
              <div className="card-body p-3">

                <h4 className="text-sm ">
                  {orgname}
                </h4>
              </div>
            </div>
          </div>
          <div className="col-12 col-xl-3">
            <button className="btn bg-gradient-info w-50 my-4" onClick={syncData}>Sync Data</button>
          </div>
        </div>
      </div>
      <ToastContainer />
    </Aux>
  );
};

export default Home;
