import axios from 'axios';
import { toast } from 'react-toastify'
import * as actionTypes from './types'
import 'react-toastify/dist/ReactToastify.css';

export const isSuccess = (value) => {
    console.log(value)
    return (dispatch) => dispatch({
        type: actionTypes.SUCCESS,
        payload: value
    })
}


export const CameraAction = (data) => {
    return {
        type: actionTypes.CAMERAACTION,
        payload: data,
    }
}

export const synData = (data) => {
    return {
        type: actionTypes.SYNCDATA,
        payload: data,
    }
}

export const dids = (data) => {
    return {
        type: actionTypes.DID,
        payload: data,
    }
}

export const ResetLogin = () => {
    return (dispatch) => dispatch({
        type: actionTypes.LOGIN,
        payload: ""
    })
}

export const LoginAction = (body, history) => {
    return (dispatch) => {
        const URL = 'http://202.140.138.33/api/api/vbas/UserLogin';
        const headers = {
            headers: {
                "Content-Type": "application/json",
                'apikey': ' ',
            },
            // body: JSON.stringify({ "username": userName, "password": Password, "macid": "11:11:11:11:11", "appver": "1.0" })
        };
        axios.post(URL, body, headers)
            .then(response => {
                if (response.data.respcode === '200') {
                    localStorage.setItem('orgid', response.data.orgid)
                    localStorage.setItem('did_from_login', response.data.did)
                    localStorage.setItem('orgname', response.data.orgname)
                    // history.push("/home")
                    toast.success(response.respdesc);
                    dispatch({
                        type: actionTypes.LOGIN,
                        payload: response.data?.respcode
                    })
                    // history.replace('/home');
                }
                else {
                    toast.error("Login Failed !!");
                }
            })
            .catch(err => {
                console.log(err);
            })
    }
}

export const SyncDataAction = (body) => {
    isSuccess(true)
    return (dispatch) => {
        const URL = 'http://202.140.138.33/api/api/vbas/SyncAttendeesData';
        const headers = {
            headers: {
                "Content-Type": "application/json",
                'apikey': ' ',
            }
        };
        axios.post(URL, body, headers)
            .then(response => {
                if (response.data.respcode === '200') {

                    // toast.success("success");
                    localStorage.setItem('syncdata', JSON.stringify(response.data.attendeesdetails));
                    console.table(response.data.attendeesdetails)
                    // localStorage.setItem('ftemplate_from_login', response.data.attendeesdetails) 
                    // console.log(response.data.attendeesdetails[0])
                    // toast.success(response.data.respdesc);
                    dispatch({
                        type: actionTypes.SYNCDATA,
                        payload: response.data.attendeesdetails
                    })
                    // dispatch(SyncDataAction());
                }
                else {
                    toast.error(response.data.respdesc);
                }
                isSuccess(false)

            })
            .catch(err => {
                console.log(err);
                isSuccess(false)
            })

    }
}

export const getSubOrgList = (body) => {
    return (dispatch) => {
        const URL = 'http://202.140.138.33/api/api/vbas/GetSubOrganisationList?';
        const headers = {
            headers: {
                "Content-Type": "application/json",
                'apikey': ' ',
            }
        };
        axios.post(URL, body, headers)
            .then(response => {
                if (response.data.respcode === '200') {
                    dispatch({
                        type: actionTypes.SUBORGS,
                        payload: response.data?.suborgs
                    })
                }
                else {
                    toast.error("Please Refrest Page");
                }
            })
            .catch(err => {
                console.log(err);
            })

    }
}

export const ReigstrationAction = (body) => {
    return (dispatch) => {
        const URL = 'http://202.140.138.33/api/api/vbas/RegisterAttendee?';
        const headers = {
            headers: {
                "Content-Type": "application/json",
                'apikey': ' ',
            }
        };
        axios.post(URL, body, headers)
            .then(response => {
                if (response.data.respcode === '200') {
                    toast.success(response.data.respdesc)
                    dispatch({
                        type: actionTypes.REGISTER,
                        payload: response.data?.RegisterReducer
                    })
                }
                else {
                    toast.error(response.data.respdesc);
                }
            })
            .catch(err => {
                console.log(err);
            })

    }
}

