export const MarkAttendeeAction = (body) => {
    return (dispatch) => {
        const URL = 'http://202.140.138.33/api/api/vbas/SyncAttendanceLogs';
        const headers = {
            headers: {
                "Content-Type": "application/json",
                'apikey': ' ',
            }
        };
        axios.post(URL, body, headers)
            .then(response => {
                if (response.data.logsack[0].respcode === '200') {
                    toast.success("success")
                    dispatch({
                        type: actionTypes.MARKATTENDEE,
                        payload: response.data
                    })
                }
                else {
                    toast.error(response.data.logsack[0].respdesc);
                }
            })
            .catch(err => {
                console.log(err);
            })

    }
}

