export const FaceEmbedAction = (body) => {
    isSuccess(true)
    return (dispatch) => {
        const URL = 'http://localhost:7000/embed';
        const headers = {
            headers: {
                "Content-Type": "application/json",
            }
        };
        axios.post(URL, body, headers)
            .then(response => {
                if (response.data.respcode === '200') {
                    toast.success('Success')
                    dispatch({
                        type: actionTypes.FACEEMBEDTYPE,
                        payload: response.data
                    })
                }
                else {
                    toast.error(response.data.respdesc);
                }
                isSuccess(false)
            })
            .catch(err => {
                console.log(err);
                isSuccess(false)

            })

    }
}