import React, { useEffect, useState } from 'react';
import 'react-toastify/dist/ReactToastify.css';
import { ToastContainer } from 'react-toastify';
import { getSubOrgList, ReigstrationAction } from '../../Store/Actions/action';
import { useDispatch, useSelector } from 'react-redux';
import Aux from '../../hoc/Aux';


const Registration = () => {
    const [userData, setUserData] = useState({ orgid: '', attendeName: '', idCode: '', email: '', number: '', nodal: '0', attendeCategory: '', gender: '' });
    const dispatch = useDispatch();
    const myState = useSelector((state) => state.vbas_store)

    const getSubOrg = async () => {
        const did = localStorage.getItem('did_from_login')
        const data = { "did": did }
        dispatch(getSubOrgList(data))
    }

    useEffect(() => {
        getSubOrg();
    }, [])

    const handleSubmit = async () => {
        const data = { "orgid": localStorage.getItem('orgid'), "did": localStorage.getItem('did_from_login'), "attdcode": userData.idCode, "attdname": userData.attendeName, "attdcategory": userData.attendeCategory, "mobileno": userData.number, "email": userData.email, "isnodaloff": userData.nodal, "gender": userData.gender }
        dispatch(ReigstrationAction(data))
        setUserData({ orgid: '', attendeName: '', idCode: '', email: '', number: '', nodal: '0', attendeCategory: '', gender: '' })
    }

    const formFields = [
        {
            size: 12,
            field: (
                <>
                    <div className="col-md-6">
                        <div className="form-group">
                            <label htmlFor="Select1">School Organization Name</label>
                            <select className="form-control" id="Select1" onChange={(e) => setUserData({ ...userData, orgid: e.target.value })} value={userData.orgid} >
                                {myState.subOrgs.map((value) => {
                                    const { orgid, orgname } = value;
                                    return (
                                        <option value={orgid}>{orgname}</option>
                                    );
                                })}
                                {/* <MenuItem value={myState.subOrgs.orgid}>{myState.subOrgs.orgname}</MenuItem> */}
                            </select>
                        </div>
                    </div>
                    <div className="col-md-6">
                        <div className="form-group">
                            <label htmlFor="Input1">Attendee Name</label>
                            <input type="text" onChange={(e) => setUserData({ ...userData, attendeName: e.target.value })} value={userData.attendeName} className="form-control" id="Input1" />
                        </div>
                    </div>
                </>
            ),
        },
        {
            size: 12,
            field: (
                <>
                    <div className="col-md-6">
                        <div className="form-group">
                            <label htmlFor="Input2">Attendee ID/Code</label>
                            <input type="text" onChange={(e) => setUserData({ ...userData, idCode: e.target.value })} value={userData.idCode} className="form-control" id="Input2" />
                        </div>
                    </div>
                    <div className="col-md-6">
                        <div className="form-group">
                            <label htmlFor="Input3">Email</label>
                            <input type="email" onChange={(e) => setUserData({ ...userData, email: e.target.value })} value={userData.email} className="form-control" id="Input3" placeholder="abc@email.com" />
                        </div>
                    </div>
                </>
            ),
        },
        {
            size: 12,
            field: (
                <>
                    <div className="col-md-6">
                        <div className="form-group">
                            <label htmlFor="Input4">Mobile Number</label>
                            <input type="number" value={userData.number} onChange={(e) => setUserData({ ...userData, number: e.target.value })} className="form-control" id="Input4" placeholder="+91 8120 687 130" />
                        </div>
                    </div>
                    <div className="col-md-6">
                        <div className="form-group">
                            <label htmlFor="Select2">Attendee Category</label>
                            <select className="form-control" id="Select2" onChange={(e) => setUserData({ ...userData, attendeCategory: e.target.value })} value={userData.attendeCategory} >
                                <option value="1">Attendee</option>
                                <option value="2">Student</option>
                                <option value="3">Staff</option>
                                <option value="4">Employee</option>
                            </select>
                        </div>
                    </div>
                </>
            ),
        },

        {
            size: 12,
            field: (
                <div className="col-md-6">
                    <label className="custom-control-label">Gender</label>
                    <div className="form-check">
                        <input value={userData.gender} onChange={(e) => setUserData({ ...userData, gender: 'M' })} className="form-check-input" type="radio" name="flexRadioDefault" id="customRadio1" />
                        <label className="custom-control-label" for="customRadio1">Male</label>
                    </div>
                    <div className="form-check mb-3">
                        <input value={userData.gender} onChange={(e) => setUserData({ ...userData, gender: 'G' })} className="form-check-input" type="radio" name="flexRadioDefault" id="customRadio2" />
                        <label className="custom-control-label" for="customRadio2">Female</label>
                    </div>
                </div>
            ),
        },
        {
            size: 12,
            field: (
                <>
                    <div className="col-md-6">
                        <div className="form-check">
                            <input value={userData.nodal} onChange={(e) => setUserData({ ...userData, nodal: '1' })} className="form-check-input" type="checkbox" defaultValue id="fcustomCheck1" defaultChecked />
                            <label className="custom-control-label" htmlFor="customCheck1">Is Nodal Officer</label>
                        </div>
                    </div>
                </>
            ),
        },

    ];

    return (
        <Aux>
            <div className="container-fluid py-4">
                <div className="row">
                    <div className="col-12">
                        <div className="card ">
                            <div className="card-header pb-0 p-3">
                                <h6 className="mb-0">Attendee Registration</h6>
                            </div>
                            <div className="card-body p-3">
                                <form>
                                    {/* OrgName */}
                                    {formFields.map((item, idx) => (
                                        <div className="row" item xs={item.size} key={idx}>
                                            {item.field}
                                        </div>
                                    ))}
                                    <div className="text-center">
                                        <button type="button" onClick={handleSubmit} className="btn bg-gradient-info w-100 mt-4 mb-0">Register</button>
                                    </div>
                                </form>
                            </div>
                        </div>
                    </div>
                </div>
            </div>
            <ToastContainer />
        </Aux>
    );
}

export default Registration;