import * as actionTypes from '../Actions/types'

const INITIAL = {
    cameraRed: [],
    Login: {},
    Did: "",
    SynData: [],
    subOrgs : [],
    RegisterReducer: {},
    FaceGetDetails: {},
    EnrollFaceReducer: [],
    MarkAttendeReducer: [],
    FaceEmbedReducer: "",
    MarkEmbedRedcuer: [],
    isSuccess: false,
};
const vbas_store = (state = INITIAL, {type, payload}) => {
    switch (type) {
        case actionTypes.CAMERAACTION: return {...state, cameraRed:payload};
        case actionTypes.LOGIN: return {...state, Login: payload};
        case actionTypes.SYNCDATA: return {...state, SynData: payload };
        case actionTypes.SUBORGS: return {...state, subOrgs: payload };
        case actionTypes.DID: return {...state, Did: payload};
        case actionTypes.REGISTER: return {...state, RegisterReducer: payload };
        case actionTypes.GETDETAILS: return {...state, FaceGetDetails: payload };
        case actionTypes.ENROLLFACE: return {...state, EnrollFaceReducer: payload };
        case actionTypes.MARKATTENDEE: return {...state, MarkAttendeReducer: payload };
        case actionTypes.FACEEMBEDTYPE: return {...state, FaceEmbedReducer: payload };
        case actionTypes.GETDETAILS: return {...state, GetDetails: payload };
        case actionTypes.MARKEMBEDTYPE: return {...state, MarkEmbedRedcuer: payload}; 
        case actionTypes.SUCCESS: return {...state, isSuccess: payload}; 


        default:
            return state;
    }
}

export default vbas_store;