import React, { useState, useEffect } from 'react';
import { useHistory } from 'react-router-dom';
import Aux from '../../hoc/Aux';
import 'react-toastify/dist/ReactToastify.css';
import { ToastContainer } from 'react-toastify';
import { useSelector, useDispatch } from "react-redux";
import { LoginAction } from '../../Store/Actions/action';

const Login = (props) => {
    // const myState = useSelector((state) => state.vbas_store)
    const [userName, setUserName] = useState("");
    const [Password, setPassword] = useState("");
    const history = useHistory();
    const dispatch = useDispatch();

    const loginSuccess = useSelector(state => state.vbas_store);
    // console.log(loginSuccess);

    useEffect(() => {
        if (loginSuccess.Login === "200") {
            history.replace('/home')
        }

    }, [loginSuccess])


    const handleLogin = async () => {
        const data = { "username": userName, "password": Password, "macid": "11:11:11:11:11", "appver": "1.0", 'devtype': "1" }
        await dispatch(LoginAction(data, history))
        // history.replace('/home')
        setUserName("")
        setPassword("")

    }

    return (
        <Aux>
            <section>
                <div className="page-header section-height-100">
                    <div className="container">
                        <div className="row">
                            <div className="col-xl-4 col-lg-5 col-md-6 d-flex flex-column mx-auto">
                                <div className="card card-plain mt-4">
                                    <div className="card-header pb-0 text-left bg-transparent">
                                        <h3 className="font-weight-bolder text-info text-gradient">Welcome back</h3>
                                        <p className="mb-0">Enter your email and password to sign in</p>
                                    </div>
                                    <div className="card-body">
                                        <form role="form text-left">
                                            <label>Email</label>
                                            <div className="mb-3">
                                                <input type="email" value={userName} onChange={(e) => setUserName(e.target.value)} className="form-control" placeholder="Email" aria-label="Email" aria-describedby="email-addon" required />
                                            </div>
                                            <label>Password</label>
                                            <div className="mb-3">
                                                <input type="password" value={Password} onChange={(e) => setPassword(e.target.value)} className="form-control" placeholder="Password" aria-label="Password" aria-describedby="password-addon" required />
                                            </div>
                                            {/* <div className="form-check form-switch">
                                                <input className="form-check-input" type="checkbox" id="rememberMe" defaultChecked />
                                                <label className="form-check-label" htmlFor="rememberMe">Remember me</label>
                                            </div> */}
                                            <div className="text-center">
                                                <button onClick={handleLogin} type="button" className="btn bg-gradient-info w-100 mt-4 mb-0">
                                                    Sign in
                                                    </button>
                                            </div>
                                        </form>
                                    </div>
                                </div>
                            </div>
                        </div>
                        <div className="col-md-6">
                            <div className="oblique position-absolute top-0 h-100 d-md-block d-none me-n8">
                                <div className="oblique-image bg-cover position-absolute fixed-top ms-auto h-100 z-index-0 ms-n6">
                                </div>
                            </div>
                        </div>
                    </div>
                </div>
            </section>
            <ToastContainer />
        </Aux>
    );
}

export default Login;



